.header{
    height: 34vw;
    margin: 30px auto;
    background: url('/public/header_img.jpg') no-repeat  ;
    background-size: 100%  100%;
    position: relative;
    width: 100%;
    /* height: 100%; */

}
.header-contents{
position: absolute;
display: flex;
flex-direction: column;
align-items: start;
gap: 1.5vw;
max-width: 60%;
bottom: 36%;
left: 6vw;
animation: fadeIn 3s;
}
.header-contents h2{
    font-weight: 500;
    color: white;
    font-size: max(4.5vw,22px);
}
.header-contents p{
    color: white;
    font-size: 1vw;
}
.header-contents button{
border: none;
color: #747474;
font-weight: 500;
padding: 1vw 2.3vw;
background-color: white;
font-size: max(1vw,13px);
border-radius: 50px;
}
.header-contents button:hover{
    color: tomato;
}




@media(max-width:1050px){
    /* .header{
        height: 38vw;
    } */
    .header-contents{
        max-width:45% ;
    }

}
@media(max-width:750px){
 
    .header-contents{
        max-width:65% ;
    }
    .header-contents p{
        display: none;
    }
    .header-contents button{
     padding: 2vw 4vw;
    }

}